import { GET_ERRORS, CLEAR_ERROR } from "../types/error.type";

const initialState = {
    msg: {},
    status: null,
    id: null,
};

const errorReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_ERRORS:
            return {
                ...state,
                msg: action.payload.msg,
                status: action.payload.status,
                id: action.payload.id,
            };
        case CLEAR_ERROR:
            return {
                ...state,
                msg: {},
                status: null,
                id: null,
            };

        default:
            return state;
    }
};

export default errorReducer;
